@import "../styles/colors.scss";

.Main {
    display: flex;
    flex-direction: column;
    gap: 80px;

    &_section {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 100%;
        &_img {
            display: flex;
            height: 30vh;
            margin-bottom: -15%;
        }

        &_text {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            padding: 16px;
            margin: 0px 16px 0px 16px;
            gap: 16px;
            background: $couleur_blanc;
            box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
            border-radius: 5px;
        }
    }

    & img {
        object-fit: cover;
    }
}


#Main_section_2{

    &_text{

    }

    &_img{
        z-index: -1;
        height: 50vh;
        margin-top: -10%;
        margin-bottom: -30%;
    }

}

#Main_section_3{

    &_text{

    }

    &_img{
        display: none;
    }

}

#Main_section_4{
    &_text{
        margin-bottom: 15%;
    }

    &_img{
        height: 50vh;
    }

}


@media screen and (min-width: 700px) {
    .Main {
        gap: 116px;
        
        &_section {
            &_img {
                height: 35vh;
                margin-bottom: -8%;
            }
            &_text{
                padding: 32px;
                margin: 0px 32px 0px 32px;
            }
        }
    }
    
    
    #Main_section_2{
        &_img{
            height: 50vh;
            margin-top: -8%;
            margin-bottom: -20%;
        }
    }
}

@media screen and (min-width: 1000px) {
    .Main {
        align-items: center;
        padding: 0px 100px 0px 100px;
        gap: 100px;

        &_section {
            max-width: 1440px;
            width: calc(100% - 160px);
            order: 1;
            display: flex;
            flex-direction: row;
            background-color: $color_blanc_casse;
            padding: 80px;
            margin: 0px 100px 0px 100px;
            gap: 80px;
            border-radius: 5px;

            &_img {
                display: flex;
                height: auto;
                width: 50%;
                margin-bottom: 0%;
                border-radius: 5px;
            }
            &_text{
                width: 50%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;
                padding: 0px;
                margin: 0px 0px 0px 0px;
                gap: 16px;
                background: transparent;
                box-shadow: none;
                border-radius: 0px;

            }
        }
    }
    
    .Carousel{
        order: 0;
    }
    
    #Main_section_2{

        &_text{
    
        }
    
        &_img{
            z-index: 0;
            height: auto;
            margin-top: 0;
            margin-bottom: 0;
        }
    
    }
    
    #Main_section_3{
        flex-direction: row-reverse;
        &_text{
    
        }
    
        &_img{
            display: block;
        }
    
    }
    
    #Main_section_4{
        margin-bottom: 100px;
        flex-direction: row-reverse;
        &_text{
            margin-bottom: 0;
        }
    
        &_img{
            height: auto;
        }
    }
}