.RechercheBar{
    position: relative;
    width: 100%;
    height: 44px;

    &_input{ 
        width: calc(100% - 16px);
        height: 100%;
        border-radius: 5px;
        border: 1px solid black;
        padding-left: 16px;
    }

    &_loupe{
        position: absolute;
        right: 16px;
        width: 18px;
        height: 44px;
    }
}