@import "./colors.scss";

body{
    color: $couleur_noir;
}

h1{
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
}

h2{
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
}

h3{
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
}

p{
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
}

a{
    color: $couleur_noir;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    &:hover{
        color: $couleur_principale;
    }
}

button{
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
}

.paragraphe_titre{
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
}

.Footer{
    color: $couleur_blanc;

    a{
        color: $couleur_blanc;
        &:hover{
            color: $couleur_principale;
        }
    }
}