@import "../../../../../styles/colors.scss";

.menu-categories{
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    transition: all .3s ease-in-out;
    

    &_active{
       display: block;
    }
}


.menu_categories_liste{
    position: relative;
    z-index: 100;
    width: 100%;
    min-height: 100%;
    background-color: $couleur_blanc;
    animation: slide-in-bottom .3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


@media screen and (min-width: 800px) {
    .menu_categories_liste{
        width: 50%;
        &_mobile{
            display: none;
        }
    }

    .menu_categories_bg{
        position: absolute;
        top: 0;
        background-color: $couleur_noir;
        opacity: 0.2;
        width: 100%;
        height: 100%;
    }
}

@keyframes slide-in-bottom {
    0% {
      transform: translateX(-300px);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
}
