@import "../../../../styles/colors.scss";

.BurgerMenu {
    width: 24px;
    height: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &:hover > .BurgerMenu_ligne{
        background-color: $couleur_principale;
    }

    &_ligne {
        background-color: $couleur_noir;
        border-radius: 5px;
        width: 24px;
        height: 3px;
        transition: all .3s ease-in-out;
        
    }
}

.ProduitBouton{
    display: flex;
    gap: 8px;
    border: none;
    background-color: transparent;

    &:hover{
        color: $couleur_principale;
    }

    &:hover .BurgerMenu_ligne{
        background-color: $couleur_principale;
    }
}