@import "../../../styles/colors.scss";

.Header {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 16px;
    width: calc(100% - 32px);
    
    &_top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }

    &_left {
        display: flex;
        align-items: center;
        gap: 16px;
        &_logo {
            width: 40%;
            & img {
                width: 100%;
            }
        }
    }

    &_center{ 
        display: none;
    }

    &_right {
        display: flex;
        gap: 1.5em;
    }
}



@media screen and (min-width: 500px) {
    .Header {
        padding: 32px;
        width: calc(100% - 64px);
    }
}

@media screen and (min-width: 550px) {
    .Header_left{
        & .BurgerMenu{
            display: none;
        }
        &_logo {
            width: 65%;
        }
    }
}

@media screen and (max-width: 550px) {
    .Header{
        & .Menu{
            display: none;
        }
    }
}

@media screen and (min-width: 800px) {
    .Header {
        padding: 32px 80px;
        width: calc(100% - 160px);
        justify-content: center;
        &_left{
            &_logo {
                width: 80%;
            }
        }

        &_center{
            display: flex;
            width: 50%;
        }

        &_bar{
            display: none;
        }
    }
}

@media screen and (min-width: 1000px) {
    .Header {
        padding: 32px 100px;
        width: calc(100% - 200px);
        &_left{
            &_logo {
                width: 100%;
            }
        }
    }
}