@import "../../../../../styles/colors.scss";

.CategoriesItem{
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 16px 32px;
    border-bottom: 1px solid $color_gris;
    position: relative;
    width: calc(100% - 64px);

    &_image{
        width: 4rem;
        border-radius: 100%;
    }

    &_decoration{
        position: absolute;
        left: 0;
        width: 5px;
        height: 100%;
        background-color: $couleur_principale;
        opacity: 0;
        transition: all 0.3s ease-in-out;
    }

    &:hover .CategoriesItem_decoration{
        opacity: 1;
    }
}

.CategoriesTitre{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    padding: 32px 32px;
    border: none;
    border-bottom: 1px solid $color_gris;
    position: relative;
    width: calc(100%);
    background-color: transparent;
    cursor: pointer;
}