@import "../../../styles/colors.scss";

.Footer{ 
    background-color: $couleur_noir;
    display: flex;
    align-items: center;
    border-radius: 15px 15px 0px 0px;
    &_zone{
        display: flex;
        flex-direction: column;
        padding: 64px 32px;
        gap: 32px;
        max-width: 1440px;  
    }
    
    &_colonne{
        display: flex;
        flex-direction: column;
        gap: 16px;

        &_links{
            display: flex;
            flex-direction: column;

        }

        &_imagePC{
            display: none;
        }
    }

    & img{
        order: 5;
        width: 100%;
        height: 100%;
    }
}

@media screen and (min-width: 550px) {
    .Footer{     
        & img{
            width: 50%;
        }
    }
}

@media screen and (min-width: 1000px) {
    .Footer{ 
        justify-content: center;
        border-radius: 0px;

        &_zone{
            width: calc(100% - 200px);
            flex-direction: row;
            justify-content: space-between;
        }
        &_colonne{
            display: flex;
            flex-direction: column;
            gap: 16px;
            
            &_links{
                display: flex;
                flex-direction: column;
    
            }

            &_imagePC{
                display: block;
                margin-bottom: 16px;
            }

            &_mobile{
                display: none;
            }
        }
    
        & img{
            order: 0;
            width: 75%;
        }
    }
}