@import "../../../../styles/colors.scss";

.CarteLiens{
    display: flex;
    height: 65vh;
    width: 100%;
    border-radius: 5px;
    position: relative;

    & img{
        object-fit: cover;
        width: 100%;
        height: 100%;
        border-radius: 5px;
    }

    &_bg{
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 50%;
        border-radius: 0px 0px 5px 5px;
        bottom: 0;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 23.92%, rgba(0, 0, 0, 0.7) 100%);
    }

    &_text{
        position: absolute;
        font-size: 24px;
        z-index: 2;
        font-weight: 500;
        color: $couleur_blanc;
        bottom: 5%;
        left: 10%;
        width: calc(100% - 10%);
    }

    &:hover{
        filter: drop-shadow(0px 0px 8px rgba(0, 0, 0, .25  ));
    }
}

@media screen and (min-width: 700px) {
    .CarteLiens{
        height: 80vh;
    }
}

@media screen and (min-width: 1000px) {
    .CarteLiens{
        height: 50vh;
        &_text{
            position: absolute;
            font-size: 24px;
            z-index: 2;
            font-weight: 500;
            color: $couleur_blanc;
            bottom: 10%;
            left: 5%;
        }
    }
}