.Titre{
    display: flex;
    align-items: center;
    gap: 16px;
    width: calc(100% - 16px);
    &_img{
        height: 10%;
    }

    & h1{
        font-size: 32px;
    }
}


@media screen and (min-width: 800px) {
    .Titre{
        & h1{
            font-size: 48px;
        }
    }
    
}

@media screen and (min-width: 1000px) {
    .Titre{
        &_img{
            height: auto;
        }
    }
    
}