@import "../../../styles/colors.scss";

.Carousel {
    width: 100%;
    overflow-x: scroll;

    &::-webkit-scrollbar {
        display: none;
    }
    &_liste {
        display: flex;
        padding-left: 16px;
        padding: 0px 16px 0px 16px;
        width: 350%;
        gap: 32px;

        &::-webkit-scrollbar {
            display: none;
        }
    }
}

@media screen and (min-width: 700px) {
    .Carousel {
        &_liste {
            padding: 0px 32px 32px 32px;
            gap: 48px;
        }
    }
}

@media screen and (min-width: 1000px) {
    .Carousel {
        width: 100%;
        max-width: 1440px;
        &_liste {
            width: calc(400%);
            animation: slide 30s infinite;
            
        }
    }
}

@keyframes slide{
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(-75%);
    }
    100% {
        transform: translateX(0);
    }
}